import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/rx';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { DataService } from '../data.service';
import { ArtistsService } from '../../services/artists/artists.service';

@Component({
  selector: 'app-price-bands',
  templateUrl: './price-bands.component.html',
  styleUrls: ['./price-bands.component.scss']
})
export class PriceBandsComponent implements OnInit {
  public percentileChart: any;
  public percentileChartOptions: any;
  public minYear: number;
  public maxYear: number;
  public selectedArtist: any;
  public artists = [];
  public sortedartistsArr = [];
  public artistName: string;
  public years: any;
  public selectedYears: any;
  public highPercentile: any;
  public lowPercentile: any;
  public disableButton: boolean;
  public max = 0;
  public tokenNotAuthenticated: boolean;

  constructor(public adb: ArtistsService, public dataService: DataService, public spinner: Ng4LoadingSpinnerService) {
    this.disableButton = false;
    this.tokenNotAuthenticated = false;
    this.highPercentile = 70;
    this.lowPercentile = 30;
    this.maxYear = (new Date().getFullYear()) - 1;
    this.minYear = 2007;
    this.years = (this.dataService.range(1922, this.maxYear + 1));
    //this.years = (this.dataService.range(this.minYear, this.maxYear + 1));
    this.adb.getTopArtists().then(res => {
      if (res['errorCode'] !== 0)
        this.tokenNotAuthenticated = true;
      else {
        this.artists = res['data'];
        
        let topartistsArr = Object.keys(this.artists).sort();
        for (let i = 0; i < topartistsArr.length; i++) {
          let temp = {};
          temp['artist_ID'] = this.artists[topartistsArr[i]];
          temp['artist_NamePrefix'] = this.artists[topartistsArr[i]].artist_NamePrefix?this.artists[topartistsArr[i]].artist_NamePrefix: '';
          temp['artist_Forename'] = this.artists[topartistsArr[i]].artist_Forename?this.artists[topartistsArr[i]].artist_Forename:'';
          temp['artist_Surname'] = this.artists[topartistsArr[i]].artist_Surname?this.artists[topartistsArr[i]].artist_Surname:'';
          temp['artist_NameSuffix'] = this.artists[topartistsArr[i]].artist_NameSuffix?this.artists[topartistsArr[i]].artist_NameSuffix:'';
          this.sortedartistsArr.push(temp);
        }
        this.selectedArtist = this.artists[0].artist_ID;
        this.getArtistsData();
      }
    });
  }

  ngOnInit() {
  }

  public getArtistsData() {
    this.spinner.show();
    let self = this;
    this.selectedYears = (this.dataService.range(this.minYear, this.maxYear + 1));

    this.percentileChartOptions = JSON.parse(JSON.stringify(this.dataService.ChartOptions));
    for (let i = 0; i < this.artists.length; i++) {
      if (this.artists[i]['artist_ID'] === this.selectedArtist) {
        this.artistName = this.artists[i].artist_Forename + " " + this.artists[i].artist_Surname;
        break;
      }
    }
    this.percentileChartOptions.title.text = this.artistName;
    this.percentileChartOptions.chart.type = "column";
    this.percentileChartOptions.xAxis.categories = this.selectedYears;
    this.percentileChartOptions.yAxis.tickWidth = 1;
    this.percentileChartOptions.yAxis.title.text = "Total Sales (in USD)";
    this.percentileChartOptions.plotOptions.column = {};
    this.percentileChartOptions.plotOptions.column.pointWidth = 10;
    this.percentileChartOptions.plotOptions.column.grouping = false;
    this.percentileChartOptions.plotOptions.column.dataLabels = {};
    this.percentileChartOptions.plotOptions.column.dataLabels.enabled = true;
    this.percentileChartOptions.plotOptions.column.dataLabels.useHTML = true;
    this.percentileChartOptions.plotOptions.column.dataLabels.crop = false;
    this.percentileChartOptions.plotOptions.column.dataLabels.overflow = 'none';
    this.percentileChartOptions.plotOptions.column.dataLabels.formatter = function () {
      let returnLabel = "<div style='width: 35px;white-space: normal;text-align: center;'>";
      returnLabel += "" + "From $" + self.dataService.convertValueIntoProperFormat(this.point.low) + " to $" + self.dataService.convertValueIntoProperFormat(this.point.high);
      returnLabel += "</div>";
      return returnLabel;
    };
    this.percentileChartOptions.tooltip = {};
    this.percentileChartOptions.tooltip.useHTML = true;
    this.percentileChartOptions.tooltip.shared = true;
    this.percentileChartOptions.tooltip.formatter = function () {
      let tooltip = "<div style='width: auto; height: auto; background-color: green !important;'>";
      tooltip += "<table><tbody>";
      tooltip += "<tr><td><span>Year: </span></td><td><span style='font-weight: bold'>" + this.x + "</span></td></tr>";
      tooltip += "<tr><td><span>Sale Volume: </span></td><td><span style='font-weight: bold'>" + this.points[1].series.userOptions.artworkCount[this.x] + "</span></td></tr>";
      tooltip += "<tr><td><span>Cut Off Lower: </span></td><td><span style='font-weight: bold'>$" + self.dataService.convertValueIntoProperFormat(this.points[0].point.low) + "</span></td></tr>";
      tooltip += "<tr><td><span>Cut Off Upper: </span></td><td><span style='font-weight: bold'>$" + self.dataService.convertValueIntoProperFormat(this.points[0].point.high) + "</span></td></tr>";
      tooltip += "<tr><td><span>Median Price: </span></td><td><span style='font-weight: bold'>$" + self.dataService.convertValueIntoProperFormat(this.points[1].y) + "</span></td></tr>";
      tooltip += "</tbody></table></div>";
      return tooltip;
    };

    let observableBatch = [];
    let percentileSeriesData = [];
    observableBatch.push(this.adb.getPercentileWiseData(this.selectedArtist, this.minYear, this.maxYear, this.highPercentile, this.lowPercentile));
    Observable.forkJoin(observableBatch).subscribe(result => {
      if (result[0]['errorCode'] !== 0) {
        this.tokenNotAuthenticated = true;
        this.spinner.hide();
      }
      else {
        const res = result[0]['data'];
        percentileSeriesData[0] = {};
        percentileSeriesData[0]['name'] = this.artistName;
        percentileSeriesData[0]['data'] = [];
        percentileSeriesData[0]['type'] = "column";

        percentileSeriesData[1] = {};
        percentileSeriesData[1]['name'] = this.artistName;
        percentileSeriesData[1]['data'] = [];
        percentileSeriesData[1]['type'] = "line";
        percentileSeriesData[1]['totalArtworks'] = {};

        for (let i = 0; i < res.length; i++) {
          let temp = {
            low: 0,
            high: 0,
            y: 0
          };

          if (this.max < +res[i]['upper'])
            this.max = +res[i]['upper'] + 5000;

          temp.low = (res[i]['lower'] === undefined || res[i]['lower'] === null || res[i]['lower'] === "" ? temp.low : +(res[i]['lower']));
          temp.high = (res[i]['upper'] === undefined || res[i]['upper'] === null || res[i]['upper'] === "" ? temp.high : +(res[i]['upper']));
          temp.y = temp.high;

          let saleDate = res[i]['YEAR(lot_SaleDate)'];
          percentileSeriesData[1]['totalArtworks']["" + saleDate] = +res[i]['total_sale'];
          percentileSeriesData[1]['data'][percentileSeriesData[1]['data'].length] = +(res[i]['median']);
          percentileSeriesData[0]['data'][percentileSeriesData[0]['data'].length] = temp;
        }
        this.drawChart(percentileSeriesData);
      }
    });
  }

  drawChart(artistData: Array<any>): void {
    while (this.percentileChart.series.length > 0)
       this.percentileChart.series[0].remove(true);

    this.percentileChart.yAxis[0].setExtremes(0, this.max);
    for (let i = 0; i < artistData.length; i++) {
      if (artistData[i].hasOwnProperty('totalArtworks'))
        this.percentileChart.addSeries({
          data: artistData[i].data,
          name: this.artistName,
          type: artistData[i].type,
          artworkCount: artistData[i]['totalArtworks']
        }, true);
      else
        this.percentileChart.addSeries({
          data: artistData[i].data,
          name: this.artistName,
          type: artistData[i].type
        }, true);
    }
    this.spinner.hide();
    this.disableButton = false;
  }

  saveInstance(chartInstance) {
    this.percentileChart = chartInstance;
  }

  decreasePercentile(percentileType) {
    this.spinner.show();
    this.disableButton = true;
    switch (percentileType) {
      case 'low':
        if (this.lowPercentile > 0)
          this.lowPercentile--;
        break;
      case 'high':
        if (this.highPercentile > 51)
          this.highPercentile--;
        break;
    }
    this.getArtistsData();
  }

  increasePercentile(percentileType) {
    this.spinner.show();
    this.disableButton = true;
    switch (percentileType) {
      case 'low':
        if (this.lowPercentile < 49)
          this.lowPercentile++;
        break;
      case 'high':
        if (this.highPercentile < 101)
          this.highPercentile++;
        break;
    }
    this.getArtistsData();
  }

  changePercentile(percentileType, event) {
    this.spinner.show();
    switch (percentileType) {
      case 'low':
        this.lowPercentile = event.value;
        break;
      case 'high':
        this.highPercentile = event.value;
        break;
    }
    this.getArtistsData();
  }
}



