import { Component, OnInit } from '@angular/core';
import { ArtistsService } from '../../services/artists/artists.service';
import * as Highcharts from 'highcharts/highmaps';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { DataService } from '../data.service';

@Component({
  selector: 'app-artists-selling',
  templateUrl: './artists-selling.component.html',
  styleUrls: ['./artists-selling.component.scss']
})

export class ArtistsSellingComponent implements OnInit {
  public map: any;
  public mapOptions: any;
  public mapData: any;
  public minYear: number;
  public maxYear: number;
  public selectedArtists: any;
  public artists = [];
  public sortedartistsArr = [];
  public years: any;
  public data: any;
  public self: this;
  public tokenNotAuthenticated: boolean;

  constructor(public dataService: DataService, public adb: ArtistsService, public spinner: Ng4LoadingSpinnerService) {
    this.tokenNotAuthenticated = false;
    this.maxYear = (new Date().getFullYear() - 1);
    this.minYear = 2007;
    // this.years = (this.dataService.range(this.minYear, this.maxYear + 1));
    this.years = (this.dataService.range(1922, this.maxYear + 1));
    this.adb.getTopArtists().then(res => {
      if (res['errorCode'] !== 0)
        this.tokenNotAuthenticated = true;
      else {
        this.artists = res['data'];
        let topartistsArr = Object.keys(this.artists).sort();
        for (let i = 0; i < topartistsArr.length; i++) {
          let temp = {};
          temp['artist_ID'] = this.artists[topartistsArr[i]];
          temp['artist_NamePrefix'] = this.artists[topartistsArr[i]].artist_NamePrefix?this.artists[topartistsArr[i]].artist_NamePrefix: '';
          temp['artist_Forename'] = this.artists[topartistsArr[i]].artist_Forename?this.artists[topartistsArr[i]].artist_Forename:'';
          temp['artist_Surname'] = this.artists[topartistsArr[i]].artist_Surname?this.artists[topartistsArr[i]].artist_Surname:'';
          temp['artist_NameSuffix'] = this.artists[topartistsArr[i]].artist_NameSuffix?this.artists[topartistsArr[i]].artist_NameSuffix:'';
          this.sortedartistsArr.push(temp);
        }
        this.selectedArtists = this.artists[0].artist_ID;
        this.getArtissellingLocation();
      }
    });
  }

  ngOnInit() {
    this.mapData = this.dataService.world_map;
  }

  getArtissellingLocation() {
    this.spinner.show();
    let self = this;
    this.adb.getArtissellingLocation(this.selectedArtists, this.minYear, this.maxYear)
      .then(data => {
        if (data['errorCode'] !== 0) {
          this.tokenNotAuthenticated = true;
          this.spinner.hide();
        }
        else {
          let countryWiseData = data['data'];
          this.data = [];
          for (let i = 0; i < countryWiseData.length; i++) {
            if (countryWiseData[i]['auction_house_country'] === 'USA')
              this.data[i] = {"key": "United States of America", "value": countryWiseData[i]['totalSale']};
            else
              this.data[i] = {"key": countryWiseData[i]['auction_house_country'], "value": countryWiseData[i]['totalSale']};
          }

          this.mapOptions = {
            title: {
              text: null
            },
            mapNavigation: {
              enableButtons: false,
              enableDoubleClickZoom: false,
              enableDoubleClickZoomTo: false,
              enableMouseWheelZoom: true,
              enableTouchZoom: false
              // enabled: false
            },
            credits: {
              enabled: false
            },
            exporting: {
              enabled: false
            },
            tooltip: {
              useHTML: true,
              formatter: function () {
                let tooltip = "<div>";
                tooltip += "<table><tbody>";
                tooltip += "<tr><td><span>Country: </span></td><td><span>" + this.point.options.key + "</span></td></tr>";
                tooltip += "<tr><td><span>Total Sales: </span></td><td><span>" + (this.point.options.value === 0 ? "$0" : "$" + this.point.options.value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")) + "</span></td></tr>";
                tooltip += "</tbody></table></div>";
                return tooltip;
              }
            },
            colorAxis: {
              min: 0,
              stops: [
                [0, '#6785ff'],
                [0.4, '#4a5ae4'],
                [0.6, '#1e27e4'],
                [0.8, '#32396a'],
                [1, '#060798']
              ],
              labels: {
                formatter: function () {
                  return self.dataService.convertValueIntoProperFormat(this.value);
                }
              }
            },
            legend: {
              layout: 'horizontal',
              align: 'right',
              verticalAlign: 'top'
            },
            series: [{
              data: this.data,
              mapData: this.mapData,
              joinBy: ['name', 'key'],
              name: '',
              states: {
                hover: {
                  color: Highcharts.getOptions().colors[2]
                }
              }
            }, {
              type: 'mapline',
              name: "Separators",
              data: Highcharts.geojson(this.mapData, 'mapline'),
              nullColor: 'gray',
              showInLegend: false,
              enableMouseTracking: false
            }]
          };
          this.spinner.hide();
        }
      });
  }

  saveInstance(chartInstance) {
    this.map = chartInstance;
  }
}



