import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
    username: string;
    constructor(public router: Router) {
        // this.username = auth.getCurrentUser().token.user;
    }
    ngOnInit() {
      this.router.navigateByUrl('/priceindex');
    }

    public onLinkClick(e){
      let type = e.tab.textLabel.toLowerCase();
      console.log('Level = ', type);
      if(type === "artists sales performance")
        this.router.navigateByUrl('/priceindex');
      else if(type === "artists price bands")
        this.router.navigateByUrl('/pricebands');
      else if(type === "bought-ins and estimates")
        this.router.navigateByUrl('/estimates');
      else  
        this.router.navigateByUrl('/artistsselling');
    }

}



