import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/rx';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { DataService } from '../data.service';
import { ArtistsService } from '../../services/artists/artists.service';


@Component({
  selector: 'app-estimates',
  templateUrl: './estimates.component.html',
  styleUrls: ['./estimates.component.scss']
})

export class EstimatesComponent implements OnInit {
  public minYear: number;
  public maxYear: number;
  public selectedArtists: any;
  public artists = [];
  public sortedartistsArr = [];
  public offerChart: any;
  public exceededChart: any;
  public offerChartOptions: any;
  public exceededChartOptions: any;
  public years: any;
  public selectedYears: any;
  public auctionHouses = [];
  public selectedAuctionHouse: any;
  public offerTitleText: string;
  public exceededTitleText: string;
  public tokenNotAuthenticated: boolean;
  public moreThanOneAuctionHouseWithSameName: boolean;
  public selectedCity: any;
  public previouslySelectedAuctionHouse: any;
  public allCompanies = {};
  public allCitiesNames = [];

  constructor(public dataService: DataService, public adb: ArtistsService, public spinner: Ng4LoadingSpinnerService) {
    this.selectedAuctionHouse = "all";
    this.tokenNotAuthenticated = false;
    this.maxYear = (new Date().getFullYear()) - 1;
    this.minYear = 2007;
    this.years = (this.dataService.range(1922, this.maxYear + 1));
    //this.years = (this.dataService.range(this.minYear, this.maxYear + 1));
    this.adb.getTopArtists().then(res => {
      if (res['errorCode'] !== 0)
        this.tokenNotAuthenticated = true;
      else {
        this.artists = res['data'];
        let topartistsArr = Object.keys(this.artists).sort();
        for (let i = 0; i < topartistsArr.length; i++) {
          let temp = {};
          temp['artist_ID'] = this.artists[topartistsArr[i]];
          temp['artist_NamePrefix'] = this.artists[topartistsArr[i]].artist_NamePrefix?this.artists[topartistsArr[i]].artist_NamePrefix: '';
          temp['artist_Forename'] = this.artists[topartistsArr[i]].artist_Forename?this.artists[topartistsArr[i]].artist_Forename:'';
          temp['artist_Surname'] = this.artists[topartistsArr[i]].artist_Surname?this.artists[topartistsArr[i]].artist_Surname:'';
          temp['artist_NameSuffix'] = this.artists[topartistsArr[i]].artist_NameSuffix?this.artists[topartistsArr[i]].artist_NameSuffix:'';
          this.sortedartistsArr.push(temp);
        }
        this.selectedArtists = this.artists[0].artist_ID;
        this.getArtistsData();
      }
    });
  }

  ngOnInit() {
    this.moreThanOneAuctionHouseWithSameName = false;
    this.adb.getTopAuctionhouses().then(result => {
      if (result['errorCode'] !== 0)
        this.tokenNotAuthenticated = true;
      else {
        let data = result['data'];
        for (let i = 0; i < data.length; i++) {
          if (!this.allCompanies.hasOwnProperty(data[i]['auction_house_CompanyID'])) {
            this.allCompanies[data[i]['auction_house_CompanyID']] = {};
            this.allCompanies[data[i]['auction_house_CompanyID']]['title'] = data[i]['auction_house_Title'];
            this.allCompanies[data[i]['auction_house_CompanyID']]['count'] = 0;
            this.allCompanies[data[i]['auction_house_CompanyID']]['location'] = {};
          }

          this.allCompanies[data[i]['auction_house_CompanyID']]['count']++;
          this.allCompanies[data[i]['auction_house_CompanyID']]['location'][data[i]['auction_house_ID']] = data[i]['auction_house_Location'];
        }

        let allHouseNames = {};
        for (let i = 0; i < data.length; i++) {
          if (!allHouseNames.hasOwnProperty(data[i]['auction_house_Title']))
            allHouseNames[data[i]['auction_house_Title']] = data[i]['auction_house_CompanyID']
        }

        let allHouseNamesArr = Object.keys(allHouseNames).sort();
        for (let i = 0; i < allHouseNamesArr.length; i++) {
          let temp = {};
          temp['companyId'] = allHouseNames[allHouseNamesArr[i]];
          temp['title'] = allHouseNamesArr[i];
          this.auctionHouses.push(temp);
        }
      }
    });
  }

  getArtistsData() {
    this.spinner.show();
    let self = this;
    this.selectedYears = (this.dataService.range(this.minYear, this.maxYear + 1));

    this.offerChartOptions = JSON.parse(JSON.stringify(this.dataService.ChartOptions));
    this.offerChartOptions.yAxis = [];
    this.offerChartOptions.yAxis[0] = {};
    this.offerChartOptions.yAxis[0].gridLineWidth = 0;
    this.offerChartOptions.yAxis[0].lineWidth = 1;
    this.offerChartOptions.yAxis[0].title = {
      text: 'Volume of Artworks on Offer',
      align: 'middle'
    };
    this.offerChartOptions.yAxis[1] = {};
    this.offerChartOptions.yAxis[1].gridLineWidth = 0;
    this.offerChartOptions.yAxis[1].lineWidth = 1;
    this.offerChartOptions.yAxis[1].lineColor = '#ff1904';
    this.offerChartOptions.yAxis[1].opposite = true;
    this.offerChartOptions.yAxis[1].title = {
      text: 'Bought-in Rate',
      align: 'middle',
      style: {
        color: '#ff1904'
      }
    };
    this.offerChartOptions.yAxis[1].labels = {};
    this.offerChartOptions.yAxis[1].labels.style = {};
    this.offerChartOptions.yAxis[1].labels.style.color = '#ff1904';
    this.offerChartOptions.yAxis[1].labels.formatter = function () {
      return this.value.toFixed(1) + "%";
    };
    this.offerChartOptions.plotOptions.line.dataLabels = {};
    this.offerChartOptions.plotOptions.line.dataLabels.enabled = true;
    this.offerChartOptions.tooltip = {};
    this.offerChartOptions.tooltip.useHTML = true;
    this.offerChartOptions.tooltip.shared = true;
    this.offerChartOptions.tooltip.formatter = function () {
      let tooltip = "<div style='background-color: #fff'>";
      tooltip += "<table><tbody>";

      if (self.selectedAuctionHouse !== "all")
        tooltip += "<tr><td colspan='2' style='text-align: center'><span style='font-weight: bold'>" +
          (self.moreThanOneAuctionHouseWithSameName && self.selectedCity !== "all" ?
            self.allCompanies[self.selectedAuctionHouse]['title'] + ", " + self.allCompanies[self.selectedAuctionHouse]['location'][self.selectedCity] :
              self.allCompanies[self.selectedAuctionHouse]['title']) + "</span></td></tr>";

      tooltip += "<tr><td><span>Year: </span></td><td><span>" + this.x + "</span></td></tr>";
      tooltip += "<tr><td><span>Bought-In Rate:</span></td><td><span>" + this.points[1].y + "%</span></td></tr>";
      tooltip += "<tr><td><span>Volume of Artworks on Offer: </span></td><td><span>" + this.points[0].y.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") + "</span></td></tr>";
      tooltip += "</tbody></table></div>";
      return tooltip;
    };

    this.exceededChartOptions = JSON.parse(JSON.stringify(this.dataService.ChartOptions));
    this.exceededChartOptions.chart.marginLeft = 80;
    this.exceededChartOptions.yAxis.title.text = "% of Times Exceeds Est. High Price";
    this.exceededChartOptions.plotOptions.line.dataLabels = {};
    this.exceededChartOptions.plotOptions.line.dataLabels.enabled = true;
    this.exceededChartOptions.plotOptions.line.dataLabels.formatter = function () {
      return this.y + "%";
    };
    this.exceededChartOptions.yAxis.labels = {};
    this.exceededChartOptions.yAxis.labels.formatter = function () {
      return this.value.toFixed(1) + "%";
    };
    this.exceededChartOptions.tooltip = {};
    this.exceededChartOptions.tooltip.useHTML = true;
    this.exceededChartOptions.tooltip.formatter = function () {
      let value = this.y;
      let tooltip = "<div style='background-color: #fff'>";
      tooltip += "<table><tbody>";

      if (self.selectedAuctionHouse !== "all")
        tooltip += "<tr><td colspan='2' style='text-align: center'><span style='font-weight: bold'>" +
          (self.moreThanOneAuctionHouseWithSameName && self.selectedCity !== "all" ?
            self.allCompanies[self.selectedAuctionHouse]['title'] + ", " + self.allCompanies[self.selectedAuctionHouse]['location'][self.selectedCity] :
              self.allCompanies[self.selectedAuctionHouse]['title']) + "</span></td></tr>";

      tooltip += "<tr><td><span>Year: </span></td><td><span>" + this.x + "</span></td></tr>";
      tooltip += "<tr><td><span>% Times Exceeded High Estimates: </span></td><td><span>" + value + "%</span></td></tr>";
      tooltip += "</tbody></table></div>";
      return tooltip;
    };

    let observableBatch = [];
    let offerSeriesData = [];
    let exceededSeriesData = [];

    if (this.selectedAuctionHouse === "all") {
      this.moreThanOneAuctionHouseWithSameName = false;
      this.selectedCity = null;
      this.allCitiesNames = [];
      observableBatch.push(this.adb.getBoughtInsEstimatefromDB(this.selectedArtists, this.minYear, this.maxYear));
    }
    else {
      let allCities;
      let companyId;
      let auctionHouseId;

      if (this.allCompanies[this.selectedAuctionHouse]['count'] > 1) {
        if (this.selectedCity === undefined || this.selectedCity === null || this.selectedCity === "all" || (this.previouslySelectedAuctionHouse !== this.selectedAuctionHouse)) {
          this.previouslySelectedAuctionHouse = this.selectedAuctionHouse;
          this.moreThanOneAuctionHouseWithSameName = true;
          this.selectedCity = "all";
          this.allCitiesNames = [];
          let sortedCityNames = [];
          let allAuctionHouseIds = Object.keys(this.allCompanies[this.selectedAuctionHouse]['location']);
          for (let i = 0; i < allAuctionHouseIds.length; i++)
            sortedCityNames[sortedCityNames.length] = this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]];

          sortedCityNames = sortedCityNames.sort();
          for (let j = 0; j < sortedCityNames.length; j++) {
            for (let i = 0; i < allAuctionHouseIds.length; i++) {
              if (this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]] === sortedCityNames[j]) {
                let temp = {};
                temp['auctionHouseId'] = allAuctionHouseIds[i];
                temp['cityName'] = this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]];
                this.allCitiesNames.push(temp);
                break;
              }
            }
          }
          allCities = true;
          companyId = this.selectedAuctionHouse;
          auctionHouseId = "";
        }
        else {
          this.moreThanOneAuctionHouseWithSameName = true;
          this.allCitiesNames = [];
          let sortedCityNames = [];
          let allAuctionHouseIds = Object.keys(this.allCompanies[this.selectedAuctionHouse]['location']);

          for (let i = 0; i < allAuctionHouseIds.length; i++)
            sortedCityNames[sortedCityNames.length] = this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]];

          sortedCityNames = sortedCityNames.sort();
          for (let j = 0; j < sortedCityNames.length; j++) {
            for (let i = 0; i < allAuctionHouseIds.length; i++) {
              if (this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]] === sortedCityNames[j]) {
                let temp = {};
                temp['auctionHouseId'] = allAuctionHouseIds[i];
                temp['cityName'] = this.allCompanies[this.selectedAuctionHouse]['location'][allAuctionHouseIds[i]];
                this.allCitiesNames.push(temp);
                break;
              }
            }
          }
          allCities = false;
          companyId = this.selectedAuctionHouse;
          auctionHouseId = this.selectedCity;
        }
      }
      else {
        this.moreThanOneAuctionHouseWithSameName = false;
        allCities = true;
        companyId = this.selectedAuctionHouse;
        auctionHouseId = "";
      }

      observableBatch.push(this.adb.getBoughtInsEstimate_AuctionHouses(this.selectedArtists, this.minYear, this.maxYear, allCities, companyId, auctionHouseId));
    }

    if (this.selectedAuctionHouse !== "all")
      this.offerTitleText = "<span>Trend : Volume of Artworks on Offer vs. Bought-ins<br>For The Selected Auction house</span>";
    else
      this.offerTitleText = "<span>Trend : Volume of Artworks on Offer / Bought-in Rate</span>";

    if (this.selectedAuctionHouse !== "all")
      this.exceededTitleText = "</span>How Often Does the Artist Exceed the Estimated High Price?<br>For the Selected Auction House</span>";
    else
      this.exceededTitleText = "<span>How Often Does the Artist Exceed the Estimated High Price?</span>";

    Observable.forkJoin(observableBatch).subscribe(result => {
      if (result[0]['errorCode'] !== 0) {
        this.tokenNotAuthenticated = true;
        this.spinner.hide();
      }
      else {
        const res = result[0]['data'];
        let categoryData = [];

        for (let i = 0; i < res.length; i++)
          categoryData[categoryData.length] = res[i]['CurrentYear'];

        offerSeriesData[0] = {};
        offerSeriesData[0].yAxis = 0;
        offerSeriesData[0].data = [];
        offerSeriesData[0].name = 'Volume of Artworks on Offer';

        offerSeriesData[1] = {};
        offerSeriesData[1].yAxis = 1;
        offerSeriesData[1].color = '#ff1904';
        offerSeriesData[1].data = [];
        offerSeriesData[1].name = 'Bought In Rate';
        offerSeriesData[1].dataLabels = {};
        offerSeriesData[1].dataLabels.enabled = true;
        offerSeriesData[1].dataLabels.formatter = function () {
          return this.y + "%";
        };

        exceededSeriesData[0] = {};
        exceededSeriesData[0].data = [];
        for (let i = 0; i < res.length; i++) {
          offerSeriesData[0].data[offerSeriesData[0].data.length] = (res[i]['TotalLot'] === undefined || res[i]['TotalLot'] === null ? 0 : res[i]['TotalLot']);
          offerSeriesData[1].data[offerSeriesData[1].data.length] = (res[i]['boughtInsCount'] !== undefined &&
            res[i]['boughtInsCount'] !== null &&
            res[i]['TotalLot'] !== undefined &&
            res[i]['TotalLot'] !== null &&
            res[i]['TotalLot'] !== 0 ? +((res[i]['boughtInsCount'] / res[i]['TotalLot']) * 100).toFixed(1) : 0);
          exceededSeriesData[0].data[exceededSeriesData[0].data.length] = (res[i]['countofExceedsEstimates'] !== undefined &&
            res[i]['countofExceedsEstimates'] !== null &&
            res[i]['soldLotCount'] !== undefined &&
            res[i]['soldLotCount'] !== null &&
            res[i]['soldLotCount'] !== 0 ? +((res[i]['countofExceedsEstimates'] / res[i]['soldLotCount']) * 100).toFixed(1) : 0);
        }

        this.spinner.hide();
        this.drawOfferChart(offerSeriesData, this.offerTitleText, categoryData);
        this.drawExceededChart(exceededSeriesData, this.exceededTitleText, categoryData);
      }
    });
  }

  drawOfferChart(artistData: Array<any>, titleText, categoryData): void {
    while (this.offerChart.series.length > 0)
       this.offerChart.series[0].remove(true);

    this.offerChart.setTitle({text: titleText, useHTML: true});
    this.offerChart.xAxis[0].setCategories(categoryData);
    for (let i = 0; i < artistData.length; i++)
      if (artistData[i].hasOwnProperty('dataLabels'))
        this.offerChart.addSeries({data: artistData[i].data, yAxis: artistData[i].yAxis, name: artistData[i].name, dataLabels: artistData[i].dataLabels, color: artistData[i].color}, true);
      else
        this.offerChart.addSeries({data: artistData[i].data, yAxis: artistData[i].yAxis, name: artistData[i].name}, true);
  }

  drawExceededChart(artistData: Array<any>, titleText, categoryData): void {
    while (this.exceededChart.series.length > 0)
       this.exceededChart.series[0].remove(true);

    this.exceededChart.setTitle({text: titleText, useHTML: true});
    this.exceededChart.xAxis[0].setCategories(categoryData);
    for (let i = 0; i < artistData.length; i++)
      this.exceededChart.addSeries({data: artistData[i].data}, true);
  }

  saveOfferInstance(chartInstance) {
    this.offerChart = chartInstance;
  }

  saveExceededInstance(chartInstance) {
    this.exceededChart = chartInstance;
  }
}



