import { Component, EventEmitter, Input, NgModule, Output, forwardRef} from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { UpgradeAdapter } from '@angular/upgrade';
import { UpgradeModule } from '@angular/upgrade/static';
import { HttpModule } from '@angular/http';
import { ChartModule } from "angular2-highcharts";
import { HighchartsStatic } from 'angular2-highcharts/dist/HighchartsService';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FileHelpersModule } from 'ngx-file-helpers';
import { D3TooltipModule } from 'ngx-d3-tooltip';
import * as angular from 'angular';
import { D3Service } from 'd3-ng2-service';

import { FlexLayoutModule } from '@angular/flex-layout';
import { BrowserAnimationsModule, NoopAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from '../material/material.module';
import { DashboardComponent } from '../pages/dashboard/dashboard.component';
import { ArtistsSellingComponent } from './artists-selling/artists-selling.component';
import { EstimatesComponent } from './estimates/estimates.component';
import { PriceBandsComponent } from './price-bands/price-bands.component';
import { PriceIndexComponent } from './price-index/price-index.component';
import { DataService } from './data.service';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { DpDatePickerModule } from 'ng2-date-picker';

declare let require: any;
export function highchartsFactory() {
    //const hc = require('highcharts');
    const dd = require('highcharts/highmaps');
    //dd(hc);
    return dd;
    //return hc;
  }
//




 // dd();
const routes: Routes = [
    { path: '', component: DashboardComponent, children: [
        { path: 'priceindex', component: PriceIndexComponent },
        { path: 'pricebands', component: PriceBandsComponent },
        { path: 'estimates', component: EstimatesComponent },
        { path: 'artistsselling', component: ArtistsSellingComponent },
    ]}
];
@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    UpgradeModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(routes),
    BrowserAnimationsModule,
    NoopAnimationsModule,
    MaterialModule,
    FlexLayoutModule,
    FileHelpersModule,
    D3TooltipModule,
    HttpModule,
    ChartModule,
    Ng4LoadingSpinnerModule.forRoot(),
    DpDatePickerModule
  ],
  providers: [
      D3Service,
      DataService, 
      {
        provide: HighchartsStatic,
        useFactory: highchartsFactory
      }
  ],
  declarations: [
      DashboardComponent,
      ArtistsSellingComponent,
      EstimatesComponent,
      PriceBandsComponent,
      PriceIndexComponent,
  ],
  exports: [ RouterModule, MaterialModule ],
  entryComponents: [ ],
})
export class PagesModule {
    constructor(public upgrade: UpgradeModule) {}
}



