import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/rx';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { DataService } from '../data.service';
import { ArtistsService } from '../../services/artists/artists.service';

@Component({
  selector: 'app-price-index',
  templateUrl: './price-index.component.html',
  styleUrls: ['./price-index.component.scss']
})


export class PriceIndexComponent implements OnInit {
  public saleChart: any;
  public volumeChart: any;
  public minYear: any;
  public maxYear: any;
  public selectedArtists = [];
  public artists = [];
  public sortedartistsArr = [];
  public artistsObj = {};
  public saleChartOptions: any;
  public volumeChartOptions: any;
  public years: any;
  public months: any;
  public weeks: any;
  public selectedYears: any;
  public tokenNotAuthenticated: boolean;
  public periodType: string;
  public minMonth: any;
  public maxMonth: any;
  public minWeek: any;
  public maxWeek: any;
  public maxMonthWeek: any;
  public minMonthWeek: any;
  public filterBy: string;
  public message: string;
  public subscription_type :string;
  public periodTypeObj = {
    0: "Yearly",
    1: "Monthly",
    2: "Weekly"
  };
  // public myDatePickerOptions = {
  //   enableMonthSelector: true,
  //   showWeekNumbers: true,
  //   disableKeypress: true,
  //   showMultipleYearsNavigation: true,
  //   max: "31/12/" + (new Date().getFullYear() - 1),
  //   min: "" + new Date("2007").getDate() + "/" + (new Date("2007").getMonth() + 1) + "/" + new Date("2007").getFullYear()
  // };

  constructor(public adb: ArtistsService, public dataService: DataService, public spinner: Ng4LoadingSpinnerService) {
    this.tokenNotAuthenticated = false;
    this.maxYear = (new Date().getFullYear()) - 1;
    this.minYear = 2007;
    this.periodType = "0";
    this.years = (this.dataService.range(1922, this.maxYear + 1));

    let monthKeys = Object.keys(this.dataService.monthObj).sort((a, b) => +a - +b);
    this.months = [];
    for (let i = 0; i < monthKeys.length; i++) {
      let temp = {};
      temp['value'] = monthKeys[i];
      temp['month'] = this.dataService.monthObj[monthKeys[i]];
      this.months.push(temp);
    }

    this.weeks = [];
    for (let i = 1; i <= 4; i++)
      this.weeks.push(i);

    this.maxMonth = "1";
    this.minMonth = "1";
    this.maxWeek = "all";
    this.minWeek = "all";

    this.adb.getTopArtists().then(res => {
      if (res['errorCode'] !== 0)
        this.tokenNotAuthenticated = true;
      else {
        this.artists = res['data'];
        
        let topartistsArr = Object.keys(this.artists).sort();
        for (let i = 0; i < topartistsArr.length; i++) {
          let temp = {};
          temp['artist_ID'] = this.artists[topartistsArr[i]];
          temp['artist_NamePrefix'] = this.artists[topartistsArr[i]].artist_NamePrefix?this.artists[topartistsArr[i]].artist_NamePrefix: '';
          temp['artist_Forename'] = this.artists[topartistsArr[i]].artist_Forename?this.artists[topartistsArr[i]].artist_Forename:'';
          temp['artist_Surname'] = this.artists[topartistsArr[i]].artist_Surname?this.artists[topartistsArr[i]].artist_Surname:'';
          temp['artist_NameSuffix'] = this.artists[topartistsArr[i]].artist_NameSuffix?this.artists[topartistsArr[i]].artist_NameSuffix:'';
          this.sortedartistsArr.push(temp);
        }
        this.selectedArtists.push(this.artists[0].artist_ID);
        this.selectedArtists.push(this.artists[1].artist_ID);
        for (let i = 0; i < this.artists.length; i++) {
          this.artistsObj[this.artists[i]['artist_ID']] = {};
          this.artistsObj[this.artists[i]['artist_ID']]['first_name'] = this.artists[i]['artist_Forename'];
          this.artistsObj[this.artists[i]['artist_ID']]['last_name'] = this.artists[i]['artist_Surname'];
        }
        this.getArtistsData();
        this.subscription_type = res['subscription_type']
        console.log('Subs Type = ', res['subscription_type']);
      }
    });
  }

  ngOnInit() {
    let self = this;
    this.saleChartOptions = JSON.parse(JSON.stringify(this.dataService.ChartOptions));
    this.saleChartOptions.yAxis.title.text = "Total Sale Value (USD)";
    this.saleChartOptions.legend.enabled = true;
    this.saleChartOptions.legend.useHTML = true;
    this.saleChartOptions.legend.labelFormatter = function () {
      let legend = "<div class='flex'>";
      legend += "<p><span class='icon-change square' style='background:" + this.color + ";'></span> " + this.name + "</p>";
      legend += "</div>";
      return legend;
    };
    this.saleChartOptions.plotOptions.series = {};
    this.saleChartOptions.plotOptions.series.events = {};
    this.saleChartOptions.plotOptions.series.events.legendItemClick = function () {
      return false;
    };
    this.saleChartOptions.tooltip = {};
    this.saleChartOptions.tooltip.useHTML = true;
    this.saleChartOptions.tooltip.formatter = function () {
      let weekly = (self.periodTypeObj[self.periodType] === "Monthly" && self.minWeek !== "all" && self.maxWeek !== "all");
      let x = this.x;
      if (weekly) {
        try {
          let year = x.split(", ")[0];
          let month = x.split(", ")[1];
          let week = (parseInt(x.split(", ")[2].split(" ")[1]) - parseInt(new Date(year, parseInt(month) - 1)["getWeek"]()));

          x = "Wk " + week + ", " + self.dataService.monthObj[month].substr(0, 3) + ", " + year;
        }
        catch (e) {}
      }

      let value = this.y;
      let tooltip = "<div>";
      tooltip += "<table><tbody>";
      tooltip += "<tr><td><span>Date Range: </span></td><td><span>" + x + "</span></td></tr>";
      tooltip += "<tr><td><span>Artist: </span></td><td><span>" + this.point.series.name + "</span></td></tr>";
      tooltip += "<tr><td><span>Total Sales (in USD): </span></td><td><span>" + (value === 0 ? 0 : value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")) + "</span></td></tr>";
      tooltip += "</tbody></table></div>";
      return tooltip;
    };
    this.saleChartOptions.xAxis.labels = {};
    this.saleChartOptions.xAxis.labels.formatter = function () {
      let weekly = (self.periodTypeObj[self.periodType] === "Monthly" && self.minWeek !== "all" && self.maxWeek !== "all");

      if (!weekly)
        return this.value;
      else {
        try {
          let year = this.value.split(", ")[0];
          let month = this.value.split(", ")[1];
          let week = (parseInt(this.value.split(", ")[2].split(" ")[1]) - parseInt(new Date(year, parseInt(month ) - 1)["getWeek"]()));

          return "Wk " + week + ", " + self.dataService.monthObj[month].substr(0, 3) + ", " + year;
        }
        catch (e) {
          return this.value;
        }
      }
    };

    this.volumeChartOptions = JSON.parse(JSON.stringify(this.dataService.ChartOptions));
    this.volumeChartOptions.yAxis.title.text = "Volume of Sales";
    this.volumeChartOptions.legend.enabled = true;
    this.volumeChartOptions.legend.useHTML = true;
    this.volumeChartOptions.legend.labelFormatter = function () {
      let legend = "<div class='flex'>";
      legend += "<p><span class='icon-change square' style='background:" + this.color + ";'></span> " + this.name + "</p>";
      legend += "</div>";
      return legend;
    };
    this.volumeChartOptions.plotOptions.series = {};
    this.volumeChartOptions.plotOptions.series.events = {};
    this.volumeChartOptions.plotOptions.series.events.legendItemClick = function () {
      return false;
    };
    this.volumeChartOptions.tooltip = {};
    this.volumeChartOptions.tooltip.useHTML = true;
    this.volumeChartOptions.tooltip.formatter = function () {
      let weekly = (self.periodTypeObj[self.periodType] === "Monthly" && self.minWeek !== "all" && self.maxWeek !== "all");
      let x = this.x;
      if (weekly) {
        try {
          let year = x.split(", ")[0];
          let month = x.split(", ")[1];
          let week = (parseInt(x.split(", ")[2].split(" ")[1]) - parseInt(new Date(year, parseInt(month) - 1)["getWeek"]()));

          x = "Wk " + week + ", " + self.dataService.monthObj[month].substr(0, 3) + ", " + year;
        }
        catch (e) {}
      }

      let value = this.y;
      let tooltip = "<div>";
      tooltip += "<table><tbody>";
      tooltip += "<tr><td><span>Year: </span></td><td><span>" + x + "</span></td></tr>";
      tooltip += "<tr><td><span>Artist: </span></td><td><span>" + this.point.series.name + "</span></td></tr>";
      tooltip += "<tr><td><span>Sale Volume: </span></td><td><span>" + (value === 0 ? 0 : value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")) + "</span></td></tr>";
      tooltip += "</tbody></table></div>";
      return tooltip;
    };
    this.volumeChartOptions.xAxis.labels = {};
    this.volumeChartOptions.xAxis.labels.formatter = function () {
      let weekly = (self.periodTypeObj[self.periodType] === "Monthly" && self.minWeek !== "all" && self.maxWeek !== "all");

      if (!weekly)
        return this.value;
      else {
        try {
          let year = this.value.split(", ")[0];
          let month = this.value.split(", ")[1];
          let week = (parseInt(this.value.split(", ")[2].split(" ")[1]) - parseInt(new Date(year, parseInt(month) - 1)["getWeek"]()));

          return "Wk " + week + ", " + self.dataService.monthObj[month].substr(0, 3) + ", " + year;
        }
        catch (e) {
          return this.value;
        }
      }
    };
  }

  getArtistsData() {
    this.message = "";
    if (this.periodTypeObj.hasOwnProperty(this.periodType)) {
      if (this.maxYear <= this.minYear && this.periodTypeObj[this.periodType] === "Yearly") {
        alert("Current selection is not Correct, Please rectify to move on");
        return;
      }

      if (this.periodTypeObj[this.periodType] === "Yearly")
        this.filterBy = "YEAR";


      if (this.periodTypeObj[this.periodType] === "Monthly") {
        if (this.minMonth === undefined || this.minMonth === null || this.minMonth === "")
          return;

        if (this.maxMonth === undefined || this.maxMonth === null || this.maxMonth === "")
          return;

        if (this.minWeek === undefined || this.minWeek === null || this.minWeek === "")
          return;

        if (this.maxWeek === undefined || this.maxWeek === null || this.maxWeek === "")
          return;

        if (new Date(this.minYear , parseInt(this.minMonth) - 1) > new Date(this.maxYear, parseInt(this.maxMonth) - 1)) {
          alert("Please choose the months appropriately");
          return;
        }

        if (new Date(this.minYear, parseInt(this.minMonth) - 1) === new Date(this.maxYear, parseInt(this.maxMonth) - 1)) {
          if (this.minWeek !== "all" && this.maxWeek !== "all" && this.minWeek >= this.maxWeek) {
            alert("Please choose the weeks appropriately");
            return;
          }
          else {
            alert("Please choose the months appropriately");
            return;
          }
        }

        if ((this.minWeek === "all" && this.maxWeek !== "all") || (this.minWeek !== "all" && this.maxWeek === "all")) {
          // alert("Please choose the weeks appropriately");
          return;
        }
        else if (this.minWeek === "all" && this.maxWeek === "all")
          this.filterBy = "MONTH";
        else
          this.filterBy = "WEEK";
      }
    }
    else
      return;

    this.spinner.show();
    this.selectedYears = (this.dataService.range(this.minYear, this.maxYear + 1));

    let observableBatch = [];
    let saleSeriesData = [];
    let volumeSeriesData = [];
    for (let i = 0; i < this.selectedArtists.length; i++)
      observableBatch.push(this.adb.getArtistsPriceIndex(this.selectedArtists[i], this.minYear, this.maxYear, this.filterBy));

    Observable.forkJoin(observableBatch).subscribe(result => {
      const res = <any[]>result;
      let combinedCategoryData = [];
      let saleSeriesDataObj = {};
      let volumeSeriesDataObj = {};
      let weekly = (this.periodTypeObj[this.periodType] === "Monthly" && this.minWeek !== "all" && this.maxWeek !== "all");

      for (let i = 0; i < res.length; i++) {
        if (res[i]['errorCode'] !== 0) {
          this.tokenNotAuthenticated = true;
          break;
        }

        if (res[i]['data'].length === 0) {
          this.message = "No data available for your selection";
          break;
        }

        let data = res[i]['data'].reverse();
        saleSeriesDataObj["" + i] = {};
        volumeSeriesDataObj["" + i] = {};
        if (this.periodTypeObj[this.periodType] === "Yearly") {
          for (let j = 0; j < data.length; j++) {
            saleSeriesDataObj["" + i]["" + data[j]['year']] = (data[j]['totalSale'] === null || data[j]['totalSale'] === undefined || data[j]['totalSale'] === "" ? 0 : +data[j]['totalSale']);
            volumeSeriesDataObj["" + i]["" + data[j]['year']] = (data[j]['totalArtworks'] === null || data[j]['totalArtworks'] === undefined || data[j]['totalArtworks'] === "" ? 0 : +data[j]['totalArtworks']);
          }
        }

        if (this.periodTypeObj[this.periodType] === "Monthly") {
          for (let j = 0; j < data.length; j++) {
            if (!saleSeriesDataObj["" + i].hasOwnProperty("" + data[j]['year']))
              saleSeriesDataObj["" + i]["" + data[j]['year']] = {};

            if (!volumeSeriesDataObj["" + i].hasOwnProperty("" + data[j]['year']))
              volumeSeriesDataObj["" + i]["" + data[j]['year']] = {};

            saleSeriesDataObj["" + i]["" + data[j]['year']]["" + (weekly ? data[j]['month'] + ", " + data[j]['week'] : data[j]['month'])] =
              (data[j]['totalSale'] === null || data[j]['totalSale'] === undefined || data[j]['totalSale'] === "" ? 0 : +data[j]['totalSale']);
            volumeSeriesDataObj["" + i]["" + data[j]['year']]["" + (weekly ? data[j]['month'] + ", " + data[j]['week'] : data[j]['month'])] =
              (data[j]['totalArtworks'] === null || data[j]['totalArtworks'] === undefined || data[j]['totalArtworks'] === "" ? 0 : +data[j]['totalArtworks']);
          }
        }
      }

      if (this.tokenNotAuthenticated) {
        this.spinner.hide();
        return;
      }

      if (this.message !== "") {
        this.spinner.hide();
        return;
      }

      let noOfArtists = Object.keys(saleSeriesDataObj);
      for (let i = 0; i < noOfArtists.length; i++) {
        if (this.periodTypeObj[this.periodType] === "Yearly")
          combinedCategoryData = combinedCategoryData.concat(Object.keys(saleSeriesDataObj[noOfArtists[i]]));

        // if (this.periodTypeObj[this.periodType] === "Monthly" || this.periodTypeObj[this.periodType] === "Weekly") {
        if (this.periodTypeObj[this.periodType] === "Monthly") {
          let years = Object.keys(saleSeriesDataObj[noOfArtists[i]]);
          for (let j = 0; j < years.length; j++) {
            let monthWeekList = Object.keys(saleSeriesDataObj[noOfArtists[i]][years[j]]);
            for (let k = 0; k < monthWeekList.length; k++) {
              if (weekly) {
                let month = monthWeekList[k].split(", ")[0];
                let week = monthWeekList[k].split(", ")[1];
                combinedCategoryData.push(years[j] + ", " + month + ", Wk " + week);
              }
              else
                combinedCategoryData.push(this.dataService.monthObj[monthWeekList[k]].substr(0, 3) + ", " + years[j]);
            }
          }
        }
      }

      if (this.periodTypeObj[this.periodType] === "Yearly")
        combinedCategoryData = combinedCategoryData.sort((a, b) => a - b);
      else {
        let collator = new Intl.Collator(undefined, {numeric: true, sensitivity: 'base'});
        combinedCategoryData = combinedCategoryData.sort(collator.compare);
      }

      for (let i = 0; i < combinedCategoryData.length; i++) {
        combinedCategoryData.splice(combinedCategoryData.indexOf(combinedCategoryData[i]),
          (combinedCategoryData.lastIndexOf(combinedCategoryData[i]) - combinedCategoryData.indexOf(combinedCategoryData[i])));
      }

      if (this.periodTypeObj[this.periodType] === "Monthly") {
        if (!weekly) {
          for (let i = 0; i < combinedCategoryData.length; i++) {
            for (let j = i; j < combinedCategoryData.length; j++) {
              if (this.calculateUniformDate(combinedCategoryData[j]) < this.calculateUniformDate(combinedCategoryData[i])) {
                let temp = combinedCategoryData[i];
                combinedCategoryData[i] = combinedCategoryData[j];
                combinedCategoryData[j] = temp;
              }
            }
          }

          let currentlySelectedMinMonthYear = ((this.dataService.monthObj["" + this.minMonth].substr(0, 3)) + ", " + this.minYear);
          let currentlySelectedMaxMonthYear = ((this.dataService.monthObj["" + this.maxMonth].substr(0, 3)) + ", " + this.maxYear);



          if (combinedCategoryData.indexOf(currentlySelectedMinMonthYear) === -1 && (this.calculateUniformDate(combinedCategoryData[0]) > this.calculateUniformDate(currentlySelectedMinMonthYear)))
            currentlySelectedMinMonthYear = combinedCategoryData[0];
          else if (combinedCategoryData.indexOf(currentlySelectedMinMonthYear) === -1 && (this.calculateUniformDate(combinedCategoryData[0]) < this.calculateUniformDate(currentlySelectedMinMonthYear))) {
            for (let i = 0; i < combinedCategoryData.length; i++) {
              if (this.calculateUniformDate(combinedCategoryData[i]) > this.calculateUniformDate(currentlySelectedMinMonthYear)) {
                currentlySelectedMinMonthYear = combinedCategoryData[i];
                break;
              }
            }
          }

          if (combinedCategoryData.indexOf(currentlySelectedMaxMonthYear) === -1 && (this.calculateUniformDate(combinedCategoryData[combinedCategoryData.length - 1]) < this.calculateUniformDate(currentlySelectedMaxMonthYear)))
            currentlySelectedMaxMonthYear = combinedCategoryData[combinedCategoryData.length - 1];
          else if (combinedCategoryData.indexOf(currentlySelectedMaxMonthYear) === -1 && (this.calculateUniformDate(combinedCategoryData[combinedCategoryData.length - 1]) > this.calculateUniformDate(currentlySelectedMaxMonthYear))) {
            for (let i = 0; i < combinedCategoryData.length; i++) {
              if (this.calculateUniformDate(combinedCategoryData[i]) > this.calculateUniformDate(currentlySelectedMaxMonthYear)) {
                currentlySelectedMaxMonthYear = combinedCategoryData[i - 1];
                break;
              }
            }
          }
          combinedCategoryData = combinedCategoryData.slice(combinedCategoryData.indexOf(currentlySelectedMinMonthYear), combinedCategoryData.indexOf(currentlySelectedMaxMonthYear) + 1);
        }
        else {
          let minMonth = this.dataService.monthObj[this.minMonth].substr(0, 3);
          let maxMonth = this.dataService.monthObj[this.maxMonth].substr(0, 3);

          let minMonthAndYear = this.calculateUniformDate(minMonth + ", " + this.minYear);
          let maxMonthAndYear = this.calculateUniformDate(maxMonth + ", " + this.maxYear);

          let currentlySelectedMinWeekYear = (this.minYear + ", " + this.minMonth + ", Wk " + (this.calculateActualWeekNumber(minMonthAndYear) + this.minWeek));
          let currentlySelectedMaxWeekYear = (this.maxYear + ", " + this.maxMonth + ", Wk " + (this.calculateActualWeekNumber(maxMonthAndYear) + this.maxWeek));

          if (combinedCategoryData.indexOf(currentlySelectedMinWeekYear) === -1) {
            if (parseInt(currentlySelectedMinWeekYear.split(", ")[0]) < parseInt(combinedCategoryData[0].split(", ")[0]))
              currentlySelectedMinWeekYear = combinedCategoryData[0];
            else if (parseInt(currentlySelectedMinWeekYear.split(", ")[0]) === parseInt(combinedCategoryData[0].split(", ")[0])) {
              if (parseInt(currentlySelectedMinWeekYear.split(", ")[1]) < parseInt(combinedCategoryData[0].split(", ")[1]))
                currentlySelectedMinWeekYear = combinedCategoryData[0];
              else if (parseInt(currentlySelectedMinWeekYear.split(", ")[1]) === parseInt(combinedCategoryData[0].split(", ")[1])) {
                if (parseInt(currentlySelectedMinWeekYear.split(", ")[2].split(" ")[1]) <= parseInt(combinedCategoryData[0].split(", ")[2].split(" ")[1]))
                  currentlySelectedMinWeekYear = combinedCategoryData[0];
                else {
                  let possibleValues = combinedCategoryData.slice(combinedCategoryData[0], 5);
                  for (let i = 0; i < possibleValues.length; i++) {
                    if (parseInt(currentlySelectedMinWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                      currentlySelectedMinWeekYear = possibleValues[i];
                      break;
                    }
                  }
                }
              }
              else if (parseInt(currentlySelectedMinWeekYear.split(", ")[1]) > parseInt(combinedCategoryData[0].split(", ")[1])) {
                let possibleValues = combinedCategoryData.filter((str) => str.indexOf(currentlySelectedMinWeekYear.split(", ")[0]) > -1 &&
                  str.indexOf(currentlySelectedMinWeekYear.split(", ")[1]) > -1);
                for (let i = 0; i < possibleValues.length; i++) {
                  if (parseInt(currentlySelectedMinWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                    currentlySelectedMinWeekYear = possibleValues[i];
                    break;
                  }
                }
              }
            }
            else if (parseInt(currentlySelectedMinWeekYear.split(", ")[0]) > parseInt(combinedCategoryData[0].split(", ")[0])) {
              let possibleValues = combinedCategoryData.filter((str) => str.indexOf(currentlySelectedMinWeekYear.split(", ")[0]) > -1);
              if (parseInt(currentlySelectedMinWeekYear.split(", ")[1]) <= possibleValues[0].split(", ")[1])
                currentlySelectedMinWeekYear = possibleValues[0];
              else {
                for (let i = 0; i < possibleValues.length; i++) {
                  if (parseInt(currentlySelectedMinWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                    currentlySelectedMinWeekYear = possibleValues[i];
                    break;
                  }
                }
              }
            }
          }

          if (combinedCategoryData.indexOf(currentlySelectedMaxWeekYear) === -1) {
            if (parseInt(currentlySelectedMaxWeekYear.split(", ")[0]) > parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[0]))
              currentlySelectedMaxWeekYear = combinedCategoryData[combinedCategoryData.length - 1];
            else if (parseInt(currentlySelectedMaxWeekYear.split(", ")[0]) === parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[0])) {
              if (parseInt(currentlySelectedMaxWeekYear.split(", ")[1]) > parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[1]))
                currentlySelectedMaxWeekYear = combinedCategoryData[combinedCategoryData.length - 1];
              else if (parseInt(currentlySelectedMaxWeekYear.split(", ")[1]) === parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[1])) {
                if (parseInt(currentlySelectedMaxWeekYear.split(", ")[2].split(" ")[1]) >= parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[2].split(" ")[1]))
                  currentlySelectedMaxWeekYear = combinedCategoryData[combinedCategoryData.length - 1];
                else {
                  let possibleValues = combinedCategoryData.slice(combinedCategoryData.length - 4, combinedCategoryData.length);
                  for (let i = 0; i < possibleValues.length; i++) {
                    if (parseInt(currentlySelectedMaxWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                      currentlySelectedMaxWeekYear = possibleValues[i];
                      break;
                    }
                  }
                }
              }
              else if (parseInt(currentlySelectedMaxWeekYear.split(", ")[1]) < parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[1])) {
                let possibleValues = combinedCategoryData.filter((str) => str.indexOf(currentlySelectedMaxWeekYear.split(", ")[0]) > -1 &&
                  str.indexOf(currentlySelectedMaxWeekYear.split(", ")[1]) > -1);
                for (let i = 0; i < possibleValues.length; i++) {
                  if (parseInt(currentlySelectedMaxWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                    currentlySelectedMaxWeekYear = possibleValues[i];
                    break;
                  }
                }
              }
            }
            else if (parseInt(currentlySelectedMaxWeekYear.split(", ")[0]) > parseInt(combinedCategoryData[combinedCategoryData.length - 1].split(", ")[0])) {
              let possibleValues = combinedCategoryData.filter((str) => str.indexOf(currentlySelectedMaxWeekYear.split(", ")[0]) > -1);
              if (parseInt(currentlySelectedMaxWeekYear.split(", ")[1]) <= possibleValues[0].split(", ")[1])
                currentlySelectedMaxWeekYear = possibleValues[0];
              else {
                for (let i = 0; i < possibleValues.length; i++) {
                  if (parseInt(currentlySelectedMaxWeekYear.split(", ")[2].split(" ")[1]) < parseInt(possibleValues[i].split(", ")[2].split(" ")[1])) {
                    currentlySelectedMaxWeekYear = possibleValues[i];
                    break;
                  }
                }
              }
            }
          }
          combinedCategoryData = combinedCategoryData.slice(combinedCategoryData.indexOf(currentlySelectedMinWeekYear), combinedCategoryData.indexOf(currentlySelectedMaxWeekYear) + 1);
        }
      }

      for (let i = 0; i < combinedCategoryData.length; i++) {
        for (let j = 0; j < noOfArtists.length; j++) {
          if (this.periodTypeObj[this.periodType] === "Yearly") {
            if (!saleSeriesDataObj[noOfArtists[j]].hasOwnProperty(combinedCategoryData[i])) {
              saleSeriesDataObj[noOfArtists[j]][combinedCategoryData[i]] = 0;
              volumeSeriesDataObj[noOfArtists[j]][combinedCategoryData[i]] = 0;
            }
          }

          if (this.periodTypeObj[this.periodType] === "Monthly") {
            if (!weekly) {
              let artistNum = noOfArtists[j];
              let year = combinedCategoryData[i].split(",")[1].trim();
              let month = Object.keys(this.dataService.monthObj)
                .find(key => this.dataService.monthObj[key].substr(0, 3) === (combinedCategoryData[i].split(", ")[0]));

              if (!saleSeriesDataObj[artistNum].hasOwnProperty(year)) {
                saleSeriesDataObj[artistNum][year] = {};
                volumeSeriesDataObj[artistNum][year] = {};
              }

              if (saleSeriesDataObj[artistNum].hasOwnProperty(year)) {
                if (!saleSeriesDataObj[artistNum][year].hasOwnProperty(month)) {
                  saleSeriesDataObj[artistNum][year][month] = 0;
                  volumeSeriesDataObj[artistNum][year][month] = 0;
                }
              }
            }
            else {
              let artistNum = noOfArtists[j];
              let year = combinedCategoryData[i].split(", ")[0].trim();
              let month = combinedCategoryData[i].split(", ")[1].trim();
              let weekNum = combinedCategoryData[i].split(", ")[2].split(" ")[1];

              if (!saleSeriesDataObj[artistNum].hasOwnProperty(year)) {
                saleSeriesDataObj[artistNum][year] = {};
                volumeSeriesDataObj[artistNum][year] = {};
              }

              if (saleSeriesDataObj[artistNum].hasOwnProperty(year)) {
                if (!saleSeriesDataObj[artistNum][year].hasOwnProperty(month + ", " + weekNum)) {
                  saleSeriesDataObj[artistNum][year][month + ", " + weekNum] = 0;
                  volumeSeriesDataObj[artistNum][year][month + ", " + weekNum] = 0;
                }
              }
            }
          }
        }
      }

      for (let i = 0; i < noOfArtists.length; i++) {
        saleSeriesData[i] = {};
        saleSeriesData[i]['name'] = this.artistsObj[this.selectedArtists[i]]['first_name'] + " " + this.artistsObj[this.selectedArtists[i]]['last_name'];
        saleSeriesData[i]['data'] = [];

        volumeSeriesData[i] = {};
        volumeSeriesData[i]['name'] = this.artistsObj[this.selectedArtists[i]]['first_name'] + " " + this.artistsObj[this.selectedArtists[i]]['last_name'];
        volumeSeriesData[i]['data'] = [];

        if (this.periodTypeObj[this.periodType] === "Yearly") {
          for (let j = 0; j < combinedCategoryData.length; j++) {
            saleSeriesData[i]['data'][saleSeriesData[i]['data'].length] = saleSeriesDataObj["" + i][combinedCategoryData[j]];
            volumeSeriesData[i]['data'][volumeSeriesData[i]['data'].length] = volumeSeriesDataObj["" + i][combinedCategoryData[j]];
          }
        }

        if (this.periodTypeObj[this.periodType] === "Monthly") {
          if (!weekly) {
            for (let j = 0; j < combinedCategoryData.length; j++) {
              let month = Object.keys(this.dataService.monthObj)
                .find(key => this.dataService.monthObj[key].substr(0, 3) === (combinedCategoryData[j].split(", ")[0]));
              saleSeriesData[i]['data'][saleSeriesData[i]['data'].length] = saleSeriesDataObj["" + i][combinedCategoryData[j].split(",")[1].trim()]["" + month];
              volumeSeriesData[i]['data'][volumeSeriesData[i]['data'].length] = volumeSeriesDataObj["" + i][combinedCategoryData[j].split(",")[1].trim()]["" + month];
            }
          }
          else {
            for (let j = 0; j < combinedCategoryData.length; j++) {
              saleSeriesData[i]['data'][saleSeriesData[i]['data'].length] = saleSeriesDataObj["" + i][combinedCategoryData[j].split(", ")[0].trim()][(combinedCategoryData[j].split(", ")[1]) + ", " + (combinedCategoryData[j].split(", ")[2].split(" ")[1])];
              volumeSeriesData[i]['data'][volumeSeriesData[i]['data'].length] = volumeSeriesDataObj["" + i][combinedCategoryData[j].split(",")[0].trim()][(combinedCategoryData[j].split(", ")[1]) + ", " + (combinedCategoryData[j].split(", ")[2].split(" ")[1])];
            }
          }
        }
      }
      this.spinner.hide();
      this.drawSaleChart(saleSeriesData, combinedCategoryData);
      this.drawVolumeChart(volumeSeriesData, combinedCategoryData);
    });
  }

  drawSaleChart(artistData: Array<any>, seriesData: Array<any>): void {
    while (this.saleChart.series.length > 0)
       this.saleChart.series[0].remove(true);

    this.saleChart.xAxis[0].setCategories(seriesData);
    for (let i = 0; i < artistData.length; i++)
      this.saleChart.addSeries({data: artistData[i].data, name: artistData[i].name}, true);
  }

  drawVolumeChart(artistData: Array<any>, seriesData: Array<any>): void {
    while (this.volumeChart.series.length > 0)
       this.volumeChart.series[0].remove(true);

    this.volumeChart.xAxis[0].setCategories(seriesData);
    for (let i = 0; i < artistData.length; i++)
      this.volumeChart.addSeries({data: artistData[i].data, name: artistData[i].name}, true);
  }

  calculateActualWeekNumber(date) {
    return Math.floor((Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()) - Date.UTC(date.getFullYear(), 0, 0)) / 24 / 60 / 60 / 1000 / 7);
  }

  calculateUniformDate(date) {
    let month = date.split(", ")[0];
    let monthNum = parseInt(Object.keys(this.dataService.monthObj).find(key => this.dataService.monthObj[key].substr(0, 3) === month)) - 1;
    let year = parseInt(date.split(", ")[1]);

    return (new Date(year, monthNum));
  }

  saveSaleInstance(chartInstance) {
    this.saleChart = chartInstance;
  }

  saveVolumeInstance(chartInstance) {
    this.volumeChart = chartInstance;
  }
}



