import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class ArtistsService {
    //To run on local machine
   // private ROOT_URL = 'http://34.232.53.0/api';
    //Production
    private ROOT_URL = 'https://interactive.blouinartsalesindex.com/api';
    private cookiedata: string;
    private cookiedataUser: string;

   constructor(private http: HttpClient, private cookieService: CookieService) {
      this.cookieService.set( 'dsbasi', '12d5b9d97c46def96e32ceca155705e8e43e7f94');
      this.cookieService.set( 'alertsUName', 'basiuser');
      this.cookiedata = this.cookieService.get('dsbasi');
      this.cookiedataUser = this.cookieService.get('alertsUName');
      console.log('cookiedataUser', this.cookiedataUser);
    }

    async getTopArtists() {
        return this.http.get(`${this.ROOT_URL}/topartists?c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }

    async getArtistsPriceIndex(artistId, minYear, maxYear, filterBy) {
        return this.http.get(`${this.ROOT_URL}/priceindex/${artistId}?minYear=${minYear}&maxYear=${maxYear}&c=${this.cookiedata}&u=${this.cookiedataUser}&filterBy=${filterBy}`).toPromise();
    }

    async getArtissellingLocation(artistID, minYear, maxYear) {
        return this.http.get(`${this.ROOT_URL}/sellinglocation/${artistID}?minYear=${minYear}&maxYear=${maxYear}&c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }

    async getBoughtInsEstimatefromDB(artistID, minYear, maxYear) {
        return this.http.get(`${this.ROOT_URL}/geteboughtinsestimates/${artistID}?minYear=${minYear}&maxYear=${maxYear}&c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }

    async getTopAuctionhouses() {
        return this.http.get(`${this.ROOT_URL}/topauctionhouses?c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }

    async getBoughtInsEstimate_AuctionHouses(artistID, minYear, maxYear, allCities, companyId, auctionHouseId) {
        return this.http.get(`${this.ROOT_URL}/geteboughtinsestimatesauctionhouses/${artistID}?minYear=${minYear}&maxYear=${maxYear}&allCities=${allCities}&cid=${companyId}&aid=${auctionHouseId}&c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }

    async getPercentileWiseData(artistID, minYear, maxYear, upper, lower) {
        return this.http.get(`${this.ROOT_URL}/getsellingbands/${artistID}?minYear=${minYear}&maxYear=${maxYear}&upper=${upper}&lower=${lower}&c=${this.cookiedata}&u=${this.cookiedataUser}`).toPromise();
    }
}



